.contacts {
  margin-bottom: 60px;

  &__inner {}

  &__top {
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

    @include mw(1000) {
      flex-direction: column;
      align-items: flex-start; } }

  &__title {

    @include mw(1000) {
      margin-bottom: 20px; } }

  &__phone {

    @include mw(1000) {
      margin-bottom: 20px; } }

  &__term {
    margin-bottom: 5px; }

  &__data {
    font-weight: 600; }

  &__link {

    @include mw(1000) {
      align-self: center; }

    @include mw(768) {
      width: 100%;
      text-align: center; } }

  &__map {
    height: 480px;
    border-radius: 20px;
    overflow: hidden; } }

ymaps.ymaps-2-1-78-ground-pane {
    filter: grayscale(1); }
