.footer {

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 38px;
    padding-bottom: 38px;
    background-image: url(/img/footer-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 67% center;

    @include mw(1000) {
      flex-direction: column; }

    @include mw(768) {
      padding-top: 0;
      background-image: none; } }

  &__contacts {
    display: flex;
    align-items: center;

    @include mw(1000) {
      margin-bottom: 30px;
      width: 100%;
      justify-content: center; } }

  &__phone {
    margin-right: 40px;

    @include mw(768) {
      display: none; } }

  &__social {

    & .socials__link {
      width: 30px;
      height: 30px; }

    @include mw(768);

    & .socials__item {

      & + li {
        margin-left: 10px;

        @include mw(768) {
          margin-left: 20px; } } } }

  &__link {
    margin-left: 40px;

    @include mw(1000) {
      margin-bottom: 30px;
      margin-left: 0; }

    @include mw(768) {
      width: 100%;
      text-align: center; } }

  &__content {
    margin-left: auto;

    @include mw(1000) {
      margin-left: 0; }

    @include mw(768) {
      align-self: flex-start; } }

  &__copy {
    margin-bottom: 10px;
    font-weight: 600;
    color: #46404C; }

  &__link-politic {
    font-weight: 600;
    color: #8D8D93; } }

