.result {
  margin-bottom: 100px;

  @include mw(768) {
    margin-bottom: 50px; }

  &__top {
    display: flex;
    align-items: flex-end;
    margin-bottom: 48px;

    @include mw(768) {
      flex-direction: column;
      align-items: flex-start; } }

  &__title {

    @include mw(768) {
      margin-bottom: 30px; } }

  &__text {
    margin-left: 10%;
    max-width: 422px;
    color: #6D6D6D;

    @include mw(768) {
      margin-left: 0; } }

  &__list {

    @include mw(600) {
      max-width: 292px;
      margin-left: auto;
      margin-right: auto; }

    & .slick-arrow {
      position: absolute;
      width: 75px;
      height: 75px;
      border-radius: 50%;
      border: 1px solid #E5D5C5;
      background-color: transparent;
      font-size: 0;
      background-image: url(/img/svg/arr-result.svg);
      background-position: center center;
      background-size: 30px 12px;
      background-repeat: no-repeat;
      cursor: pointer;
      top: -150px;

      @include mw(768) {
        top: 50%;
        transform: translateY(-50%);
        width: 30px;
        height: 30px;
        background-size: 20px 8px; } }

    & .slick-next {
      right: 0;
      transform: rotate(180deg);

      @include mw(768) {
        transform: rotate(180deg) translateY(50%);
        z-index: 1;
        right: 10px; } }

    & .slick-prev {
      right: 80px;
      z-index: 1;

      @include mw(768) {
        left: 10px;
        right: auto; } } }

  &__item {
    cursor: pointer;

    &:focus {
      outline: none; } }

  &__link {
    display: block;
    position: relative;

    &--eventnone {
      pointer-events: none; }

    &::after {
      position: absolute;
      content: "";
      width: 92px;
      height: 92px;
      background-color: #fff;
      border-radius: 50%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background-image: url(/img/svg/you.svg);
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 20px 23px; } }

  &__img {
    border-radius: 10px;
    display: block;

    &:focus {
      outline: none; } } }

